.page__body {
  margin: 0;
  font-family: Roboto, sans-serif, system-ui;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  display: block;
}

.nav {
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__item {
  list-style: none;
}

.nav > :not(:first-child) {
  margin-left: 20px;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  height: 61px;
  margin-top: -1px;
  font-size: 12px;
  font-weight: 500;
  line-height: 61px;
  text-decoration: none;
  transition: all .3s;
  display: inline-block;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.catalog {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

.product-card {
  box-sizing: border-box;
  color: #060b35;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  width: 200px;
  padding: 16px;
  transition: all .3s;
  display: flex;
}

.product-card__image-block {
  margin: auto;
}

.product-card__image {
  width: 160px;
  height: 134px;
  margin-top: 16px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.product-card__title {
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product-card__product-code {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.product-card__reviews {
  justify-content: space-between;
  align-items: end;
  margin-top: 16px;
  display: flex;
}

.product-card__reviews-count {
  font-size: 10px;
  line-height: 14px;
}

.product-card__price-block {
  justify-content: space-between;
  margin-top: 24px;
  display: flex;
}

.product-card__price-text {
  color: #616070;
  margin: 0;
  font-size: 12px;
  line-height: 18px;
}

.product-card__price {
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product-card__button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition: all .3s;
}

.product-card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.product-card:hover {
  transform: scale(1.2);
}

.product-card:hover .product-card__title {
  color: #34568b;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-of-type(-n+3), .stars--4 .stars__star:nth-of-type(-n+4), .stars--5 .stars__star:nth-of-type(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
